import React from 'react';
import Bio from './bio';
import Footer from './footer';
import { Link } from 'gatsby';
import { scale } from '../utils/typography';
import '../global.css';

const Layout = ({ author, location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`;
  let header;

  if (location.pathname === rootPath) {
    header = (
      <div
        style={{
          display: `flex`,
          flexDirection: `column`,
          alignItems: `center`,
          maxWidth: `700px`,
        }}
      >
        <h1
          style={{
            ...scale(1.5),
            textAlign: `center`,
          }}
        >
          <div className={`title`}>{title}</div>
        </h1>
        <Bio />
        <img
          src={require(`../../content/assets/mobile_dev.png`)}
          alt={`down arrow icon`}
          className={`cartoon`}
        />
        <a href={`#main`} style={{ position: `absolute`, bottom: 0 }}>
          <img
            src={require(`../../content/assets/down_chevron.png`)}
            alt={`down arrow icon`}
            className={`arrow`}
          />
        </a>
      </div>
    );
  } else {
    header = (
      <h1
        style={{
          ...scale(1.5),
          textAlign: `center`,
        }}
      >
        <Link className={`title`} to={`/`}>
          {title}
        </Link>
      </h1>
    );
  }
  return (
    <div
      style={{
        marginLeft: `auto`,
        marginRight: `auto`,
        maxWidth: `1000px`,
      }}
    >
      <header>{header}</header>
      <main id={`main`}>{children}</main>
      <Footer />
    </div>
  );
};

export default Layout;
